.mentoring {
  padding: 60px 0px;
}

.mentoring .card-item {
  border: 1px solid rgba(82, 86, 94, 0.2);
  background: #fff;
  position: relative;
  border-radius: 0;
}

.mentoring .card-item .card-bg {
  min-height: 300px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.mentoring .card-item .card-body {
  padding: 30px;
}

.mentoring .card-item h4 {
  font-weight: 700;
  font-size: 20px;
  margin-bottom: 15px;
  color: var(--color-primary);
}

.mentoring .card-item p {
  color: #7f7b77;
  margin: 0;
}
