.description {
  background-color: #eff7ff;
  padding: 60px 0px;
}

/* Estilo geral para alinhar o bullet e o título na mesma linha */
.li-space-lg li {
  display: flex;
  align-items: flex-start; /* Alinha o bullet e o título no topo */
  margin-bottom: 2.5rem; /* Aumenta o espaçamento entre os itens */
}

/* Estilo do bullet (círculo ao lado do título) */
.bullet {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 0.625rem;
  border-radius: 50%;
  background-color: var(--color-secondary);
  width: 4.125rem;
  height: 4.125rem;
  font-style: normal;
  line-height: 2rem;
  color: var(--color-primary);
  font-weight: 700;
  font-size: 1.875rem;
  text-align: center;
  padding: 24px;
}

.media-body {
  display: flex;
  flex-direction: column;
}

.media-body h4 {
  padding-left: 15px;
  margin: 0;
  font-size: 24px;
}

.media-body p {
  padding-left: 15px;
  margin: 0.5rem 0 0 0;
  font-size: 20px;
}
