/* button style */

.btn {
  border-radius: 4px;
  color: #fff;
  display: inline-block;
  font-size: 16px;
  letter-spacing: 0;
  line-height: 1;
  margin-bottom: 0;
  padding: 20px 30px;
  text-align: center;
  text-transform: unset;
  touch-action: manipulation;
  transition: all 0.3s ease 0s;
  vertical-align: middle;
  white-space: nowrap;
  position: relative;
  z-index: 1;
  text-transform: capitalize;
  background: var(--color-secondary);
  border: 1px solid var(--color-secondary);
  overflow: hidden;
}

.btn i {
  margin-left: 10px;
  font-size: 18px;
}
.btn::before {
  width: 40px;
  height: 40px;
  position: absolute;
  border-radius: 50%;
  content: "";
  background: var(--color-primary);
  top: -60px;
  left: 10px;
  z-index: -1;
  transition: all 0.3s ease 0s;
}
.btn:hover::before {
  width: 100%;
  height: 100%;
  position: absolute;
  border-radius: 4px;
  content: "";
  background: var(--color-primary);
  top: 0;
  left: 0;
  z-index: -1;
}
.btn:hover {
  color: #fff;
  border-color: var(--color-primary);
}

