}
.animations-02 {
  position: absolute;
  right: 0;
  bottom: -10px;
  animation: alltuchtopdown 5s infinite;
  -webkit-animation: alltuchtopdown 5s infinite;
  animation-delay: 0s;
  -webkit-animation-delay: 3s;
}
.animations-07 {
  position: absolute;
  left: 0px;
  bottom: -50px;
  animation: alltuchtopdown 3s infinite;
  -webkit-animation: alltuchtopdown 3s infinite;
